export default {
  data() {
    return {
      loading: false,
      form: {
        email: {
          type: 'text',
          val: '',
          placeholder: 'Email...',
          icon: 'fas fa-envelope',
          id: 'sentResetEmail',
        },
      },
      errors: {},
      status: '',
      alert: {
        class: '',
        show: false,
        text: '',
      },
    }
  },
  methods: {
    async formSubmit() {
      this.errors = {}
      this.alert = {}
      this.loading = true
      try {
        await this.$axios
          .$post('/password/email', {
            email: this.form.email.val,
          })
          .then((res) => {
            this.status = res.status
          })
        this.alert.class = 'alert-success'
        this.alert.show = true
        this.alert.text = this.status
      } catch (e) {
        if (e.response.data?.errors?.message?.includes('already verified')) {
          this.alert.show = true
          this.alert.class = 'alert-danger'
          this.alert.text =
            e.response.data.errors.message +
            ` <strong>(${this.form.email.val})</strong>`
        } else if (e.response.data?.email) {
          this.alert.show = true
          this.alert.class = 'alert-danger'
          this.alert.text = e.response.data.email
        } else {
          this.errors = e.response.data.errors
        }
      } finally {
        this.loading = false
      }
    },
    clear() {
      Object.assign(this.$data, this.$options.data())
    },
  },
}
